let Mcaptcha = require('../../utils/captcha.js');
// import UniSInput from '../../components/s-input/s-input.vue';

export default {
	// components: {
	//     UniSInput
	// },
	data() {
		return {
			shadow:false,
			name:'',
			wx:'',
			phone:'',
			need:'',
			num:'',
			codeNum:'',
		}
	},
	onLoad() {

	},
	onShow() {
		this.changeCaptcha();
	},
	methods: {
		showShadow(){
			this.shadow=true;
		},
		closeShadow(){
			this.shadow=false;
		},
		//显示验证码
		getRanNum() {
		    var chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';
		    var pwd = '';
		    for (var i = 0; i < 4; i++) {
		      if (Math.random() < 48) {
		        pwd += chars.charAt(Math.random() * 48 - 1);
		      }
		    }
		    return pwd;
		},
		changeCaptcha(){
			var num = this.getRanNum();
			this.num = num.toLowerCase();
			console.log(this.num);
			new Mcaptcha({
			      el: 'canvas',
			      width: 90,//对图形的宽高进行控制
			      height: 40,
			      code: num
			});
		},
		
		//输入姓名
		nameInput(e){
			this.name = e.detail.value
		},
		//微信
		wxInput(e){
			this.wx = e.detail.value
		},
		//手机号
		phoneInput(e){
			this.phone = e.detail.value
		},
		//需求
		requireInput(e){
			this.need = e.detail.value
		},
		//输入验证码
		codeInput(e){
			this.codeNum = e.detail.value.toLowerCase();
			// console.log(this.codeNum)
		},
		
		//校验内容
		checkInput(){
			if(this.name == '' || this.wx == '' || this.phone == '' || this.require == ''){
				uni.showToast({
					title:'各项内容不能为空！',
					icon:"none",
				})
			}else{
				this.checkCode();
			}
			
			
		},
		//校验验证码
		checkCode(){
			if(this.num == this.codeNum){
				 this.submit();
				// console.log('suc');
				
				this.changeCaptcha();
			}else{
				this.changeCaptcha();
				uni.showToast({
					title:'验证码错误!',
					icon:"none",
				})
			}
		},
		//提交需求
		 submit(){
			uni.getStorage({
				key:'openid',
				success:(res)=>{
					this.confim(res.data)
				},
				fail: (err) => {
					this.getUserInfo()
				}
			})
		},
		
		getUserInfo(){
			uni.login({
				success:res =>{
					if(res.code){
						uni.getUserInfo({
							withCredentials:true,
							success: (res_user) => {
								this.getOpenId(res.code);
							},
							
						})
					}else{
						
					}
				}
			})
		},
		async getOpenId(code){
			var data = {
				 js_code:code,
			};
			// console.log(code);
			// var res = await this.$request.get('http://127.0.0.1:5000/getUser',data);
			var res = await this.$request.get("https://api.weixin.qq.com/sns/jscode2session?appid=wxddfdc0207f9685cc&secret=46c39d9ef7186ded35c917314b298070&js_code=" + code + "&grant_type=authorization_code",);
			uni.setStorage({
				key:'openid',
				data:res.data.openid
			})
		},
		
		
		//确认提交需求
		async confim(openid){
			var data = {
				openid:openid,
				name:this.name,
				wx:this.wx,
				phone:this.phone,
				need:this.need,
				publishTime:this.$moment(new Date().getTime()).format('YYYY-MM-DD HH:mm:ss')
			};
			
			var res = await this.$request.post('/require',data);
			// console.log(res);
			if(res.code == 0){
				uni.showToast({
					title:'提交成功！',
					icon:"none",
				});
				this.name ='';
				this.wx ='';
				this.phone ='';
				this.need ='';
				this.codeNum ='';
			}else{
				uni.showToast({
					title:'失败请重试！',
					icon:"none",
				})
			}
		},
	},
	
}
